import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import validator from 'validator';

import Form from './Form';

class Newsletter extends Component {

    state = {
        emailError: null,
        email: "",
        errors: []
    }

    render() {
        let {
            emailError,
            email,
            errors
        } = this.state;
        const { t } = this.props;

        return (
            <div className="col-md col-xl-5 ml-auto">
                <Form
                    submit={this.submit}
                    errors={errors}
                    elements={() => (
                        <React.Fragment>
                            <div>
                                <p><strong>{t("main.part18")}</strong></p>
                            </div>
                            <div className="input-group">
                                <input type="text" className="form-control" placeholder="Email" name="email" value={email} onChange={this.change}/>
                                <input className="btn btn-primary" type="submit" value={t("main.part19")} />
                            </div>
                            <p style={{
                                color: 'red',
                            }}>{emailError}</p>
                        </React.Fragment>
                    )}
                />
            </div>
        );
    }

    change = (event) => {
        const { t } = this.props;
        const name = event.target.name;
        const value = event.target.value;

        if (validator.isEmail(value)) {
            this.setState({emailError: null});;
        } else {
            this.setState({emailError: t("error.invalidEmail")});;
        }

        this.setState(() => {
            return {
                [name]: value
            };
        });
    }

    submit = () => {
        const { context } = this.props;
        let { email, emailError } = this.state;

        const newsletter = { email };

        if (!emailError) {
            context.data.createNewsletter(newsletter)
                .then( errors => {
                    if (errors.length) {
                        this.setState({ errors });
                    }
                    this.setState({email: ""});
                })
                .catch((err) => {
                    console.log(err);
                });
        } else {
            console.error(emailError);
        }
    }
}

export default withTranslation()(Newsletter);
